<template lang="html">
    <div class="waybill-wrapper">
		<div class="waybill-header">
			<div class="header-zuo">
				<h2>录入运单</h2>
				<p>啦啦啦啦啦啦啦啦啦</p>
			</div>
			<p><a  onclick="window.history.go(-1)">返回上一页</a></p>
		</div>
		<div class="waybill-centent">
			<div class="centent-sj">
				司机信息检索 <el-input v-model="input" class = "sous" placeholder="请输入内容"></el-input><el-button type="primary" icon="search">搜索</el-button>
			</div>
			<div class="waybill-footer">
				<div class="footer-left">
					<div class="footer-lao">
						<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
							<!--司机信息-->
							<el-form-item label="承运司机信息" prop="age2">
							    <el-input v-model="ruleForm.age2_1" class = "xx" placeholder="1234658"></el-input>
							    <el-input v-model="ruleForm.age2_2" class = "xx" placeholder="请输入车牌号"></el-input>
							    <el-input v-model="ruleForm.age2_3" class = "xx" placeholder="请输入司机姓名"></el-input>
							</el-form-item>
							<!--运单号-->
							<el-form-item label="运单号" prop="age">
							    <el-input v-model="ruleForm.age" class = "yund"></el-input>
							</el-form-item>
							
							<!--受票方-->
						  <el-form-item label="受票方" prop="name">
						    <el-input v-model="ruleForm.name" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--发货人-->
						  <el-form-item label="发货人" prop="papa">
						    <el-input v-model="ruleForm.papa" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--联系电话-->
						  <el-form-item label="联系电话" prop="age3">
						    <el-input v-model="ruleForm.age3" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--始发城市-->
						  <el-form-item label="始发城市" prop="region" class = "select1">
						    <el-select v-model="ruleForm.region" placeholder="省/市/区" style = "width:23%">
						      <el-option label="河南省" value="shanghai"></el-option>
						      <el-option label="河北省" value="beijing"></el-option>
						      <el-option label="山西省" value="beijing"></el-option>
						    </el-select>
							<el-input v-model="ruleForm.age1" class = "age1" placeholder="请输入详细地址"></el-input>
						  </el-form-item>
						  
						  <!--收货人-->
						  <el-form-item label="收货人" prop="shou">
						    <el-input v-model="ruleForm.shou" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--联系电话-->
						  <el-form-item label="联系电话" prop="lian">
						    <el-input v-model="ruleForm.lian" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--目的城市-->
						  <el-form-item label="目的城市" prop="mudi" class = "select1">
						    <el-select v-model="ruleForm.mudi" placeholder="省/市/区" style = "width:23%">
						      <el-option label="河南省" value="shanghai"></el-option>
						      <el-option label="河北省" value="beijing"></el-option>
						      <el-option label="山西省" value="beijing"></el-option>
						    </el-select>
							<el-input v-model="ruleForm.agea" class = "age1" placeholder="请输入详细地址"></el-input>
						  </el-form-item>
						  
						  <!--货物名称-->
						  <el-form-item label="货物名称" prop="huow">
						    <el-input v-model="ruleForm.huow" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--货物数量-->
						  <el-form-item label="货物数量" prop="shul">
						    <el-input v-model="ruleForm.shul" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--运单单价-->
						  <el-form-item label="运单单价" prop="danj">
						    <el-input v-model="ruleForm.danj" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--运费金额-->
						  <el-form-item label="运费金额" prop="money">
						    <el-input v-model="ruleForm.money" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--司机运费-->
						  <el-form-item label="司机运费" prop="freight">
						    <el-input v-model="ruleForm.freight" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--上传合同-->
						  <el-form-item label="上传合同" prop="contract">
						    <el-input v-model="ruleForm.contract" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--上传回单-->
						  <el-form-item label="上传回单" prop="receipt">
						    <el-input v-model="ruleForm.receipt" class = "yund"></el-input>
						  </el-form-item>
						  
						  <!--备注-->
						  <el-form-item label="备注" prop="desc" style = "width:68%">
						    <el-input type="textarea" v-model="ruleForm.desc"></el-input>
						  </el-form-item>
						  
						  <!--保存运单-->
						  <el-form-item>
								<!-- 保存运单 -->
						     <el-button type="primary"  @click="submitForm('ruleForm')">
									 <router-link type="primary"  to='/mangement' style='color:white;'>保存运单</router-link>
								 </el-button> 
						    <el-button @click="resetForm('ruleForm')">清空</el-button>
						  </el-form-item>
						</el-form>
						
					</div>
						
				</div>
				<div class="footer-right">
					<div class="footer_centent">
						<h2>企业历史运单</h2>
						<ul class="footer_footer">
							<li>
								<template>
							  <el-radio class="radio" v-model="radio" style = "width: 20px;"></el-radio>
							</template>
							 <p>银川    </p>
							 <p>
								 <!--<i class="el-icon-d-arrow-right i"></i>
								 <i class="el-icon-d-arrow-right i"></i>
								 <i class="el-icon-d-arrow-right i"></i>-->
								 <i class="el-icon-minus i"></i>
							 </p>
							 <p>潍坊</p>
							</li>
							<li>
								<template>
							  <el-radio class="radio" v-model="radio" style = "width: 20px;"></el-radio>
							</template>
							 <p>乌鲁木齐 </p>
							 <p>
								 <!--<i class="el-icon-d-arrow-right i"></i>
								 <i class="el-icon-d-arrow-right i"></i>
								 <i class="el-icon-d-arrow-right i"></i>-->
								 <i class="el-icon-minus i"></i>
								 
							 </p>
							 <p>天津</p>
							</li>
							<li>
								<template>
							  <el-radio class="radio" v-model="radio" style = "width: 20px;"></el-radio>
							</template>
							 <p>呼和浩特 </p>
							 <p>
								 <!--<i class="el-icon-d-arrow-right i"></i>
								 <i class="el-icon-d-arrow-right i"></i>
								 <i class="el-icon-d-arrow-right i"></i>-->
								 <i class="el-icon-minus i"></i>
								 
							 </p>
							 <p>天津</p>
							</li>
							<li>
								<template>
							  <el-radio class="radio" v-model="radio" style = "width: 20px;"></el-radio>
							</template>
							 <p>银川      </p>
							 <p>
								 <!--<i class="el-icon-d-arrow-right i"></i>
								 <i class="el-icon-d-arrow-right i"></i>
								 <i class="el-icon-d-arrow-right i"></i>-->
								 <i class="el-icon-minus i"></i>
								 
							 </p>
							 <p>东营     </p>
							</li>
							<li>
								<template>
							  <el-radio class="radio" v-model="radio" style = "width: 20px;"></el-radio>
							</template>
							 <p>&nbsp;北京&nbsp;</p>
							 <p>
							 	
								 <!--<i class="el-icon-d-arrow-right i"></i>
								 <i class="el-icon-d-arrow-right i"></i>
								 <i class="el-icon-d-arrow-right i"></i>-->
								 <i class="el-icon-minus i"></i>
								 
							 </p>
							 <p>河南</p>
							</li>
							<li>输入手机号查看司机历史记录</li>
						</ul>
							
					</div>
					
				</div>
			</div>
		</div>
		
		<router-view></router-view>
    </div>
</template>
<script>
export default {
	 data() {
    return {
      input: '',
      radio: '1',
        ruleForm: {
          name: '',
          age:'',
          age2:'',
          age3:'',
					age1:'',
					agea:'',
          shou:'',
          lian:'',
          mudi:'',
          huow:'',
          shul:'',
          danj:'',
          money:'',
          contract:'',
          receipt:'',
          freight:'',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
					desc: '',
					age2_1:'',
					age2_2:"",
					age2_3:""
        },
        rules: {
    	  age2_1:[
    		{ required: true, message: '123456897', trigger: 'blur' },
            // { min: 3, max: 5, message: '长度在 3 到 5 个数字', trigger: 'blur' }
				],
				age2_2:[
    		{ required: true, message: '123456897', trigger: 'blur' },
            // { min: 3, max: 5, message: '长度在 3 到 5 个数字', trigger: 'blur' }
				],
				age2_3:[
    		{ required: true, message: '123456897', trigger: 'blur' },
            // { min: 3, max: 5, message: '长度在 3 到 5 个数字', trigger: 'blur' }
    	  ],
          name: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          mudi:[
            { required: true, message: '请输入活动名称', trigger: 'blur' },
          ],
          huow:[
          	{ required: true, message: '请输入活动名称', trigger: 'blur' },
//          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          shul:[
          	{ required: true, message: '请输入活动名称', trigger: 'blur' },
          ],
          money:[
          	{ required: true, message: '请输入活动名称', trigger: 'blur' },
          ],
          contract:[
          	{ required: true, message: '请输入活动名称', trigger: 'blur' },
          ],
          receipt:[
          	{ required: true, message: '请输入活动名称', trigger: 'blur' },
          ],
          freight:[
          	{ required: true, message: '请输入活动名称', trigger: 'blur' },
          ],
          region: [
            { required: true, message: '请选择活动区域', trigger: 'change' }
          ],
          resource: [
            { required: true, message: '请选择活动资源', trigger: 'change' }
          ]
//        desc: [
//          { required: true, message: '请填写活动形式', trigger: 'blur' }
//        ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style lang="less" scoped>
.sidebar .sidebar-right {
    width: 908px;
    height: 1361px;
    float: right;
}
	.waybill-wrapper{
    width: 908px;
		height: 1361px;
		background:#fafbff;
		padding: 0px 30px;
		.waybill-header{
			height: 90px;
			width:800px;
			display: flex;
			justify-content: space-between;
			line-height: 90px;
			/*border:1px solid #ccc;*/
			.header-zuo{
				display: flex;
				h2{
					color: #4046b6;
					margin-right: 30px;
				}
			}
			p{
				color: #ccc;
				font-size: 13px;
			}
		}
		.waybill-centent{
			width:900px;
			height: 1270px;
			border: 1px solid #ccc;
			background: #fff;
			
			.centent-sj{
				margin: 0 auto;
				height: 100px;
				width: 95%;
				border-bottom: 2px solid #ccc;
				display:flex;
				align-items: center;
				font-size:15px;
				color:#8694b7;
				font-weight: bold;
				
			}
			.sous{
					width: 300px;
					margin-left: 10px;
					margin-right: 10px;
					background: #f5f6fb;
				}
			.waybill-footer{
				padding-top: 20px;
				width: 95%;
				height: 1168px;
				margin: 0 auto;
				display: flex;
				.footer-left{
					width: 950px;
					height: auto;
					.footer-lao{
						.age1{
							width: 150px;
						}
						.xx{
							width:30%;
							margin-left: -10px;
							border-radius: 0;
						}
						.xx:first-child{
							margin: 0;
							
						}
						.yund{
							width: 60%;
						}
						.el-select{
							width:26%;
						}
						
					}
				}
				.footer-right{
					width: 320px;
					height: auto;
					/*border: 1px solid #ccc;*/
					.footer_centent{
						width: 315px;
						height: 630px;
						background: #fffcfd;
						h2{
							height: 50px;
							line-height: 50px;
							padding-left: 20px;
							border-bottom: 1px solid #ccc;
						}
						.footer_footer{
						/*width: 100%;*/
						padding: 20px;
						height: 300px;
						margin: 0 auto;
						li{
							height:40px;
							border: 1px solid #ccc;
							align-items: center;
							display: flex;
							background: #fff;
							margin-bottom: 10px;
							justify-content: space-around;
							p{
								width: 50px;
								text-align: center;
							}
							.i{
								color: orange;
								font-weight: 20px;
							}
						}
						li:last-child{
							background: #fffcfd;
							border: 0;
							/*display: flex;*/
							text-align: left;
						}
						
					}
					}
					
				}
			}
		}
	}
	
	
	
	
	
	
	
</style>



